  align-items: flex-start;
  gap: 30px;
  display: flex;
  overflow-x: hidden;
}

main {
  margin-left: 80px;
  transition: all .3s;
}

.moveToMain {
  margin-left: 530px;
}

.createPostContainer {
  width: 500px;
  height: 100vh;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 20px;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: -470px;
}

.show-create-post-container {
  background-color: rgba(0, 0, 0, .7);
  left: 0;
}

form.updatePostForm {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

form.createPostForm {
  width: 80%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

form.createCommentForm {
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  display: flex;
}

.side-bar {
  height: 100%;
  width: 30px;
  border: none;
  flex-direction: row;
  justify-content: start;
  transition: all .2s;
  display: flex;
}

.side-bar-btn {
  height: 100%;
  width: 30px;
  background-color: #dc143c;
  border-radius: 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.side-bar-btn span {
  writing-mode: vertical-rl;
  color: #fff;
}

.gradient {
  background: linear-gradient(149deg, #18bb9c 0%, #6a39af 42%, #bb1894 72%, #733586 100%) 0 0 / 400% 400%;
  animation: gradient 10s linear infinite;
}

@keyframes gradient {
  0% {
    background-position: 80% 0;
  }

  50% {
    background-position: 20% 100%;
  }

  100% {
    background-position: 80% 0;
  }
}

div#postsContainer {
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 20px;
  display: flex;
}

.post {
  width: 500px;
  height: 600px;
  border: 2px solid #000;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  gap: 5px;
  padding: 20px;
  transition: all .3s;
  display: flex;
  overflow: hidden;
}

.post .text {
  max-height: 80%;
}

.post p {
  max-width: 100%;
  max-height: 90%;
  word-wrap: break-word;
  border: 1px solid #000;
  border-radius: 7px;
  padding: 4px;
  overflow: auto;
}

.post p::-webkit-scrollbar {
  width: 6px;
}

.post p::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
}

.post p::-webkit-scrollbar-thumb {
  background-color: #a9a9a9;
}

.post:hover {
  color: #fff;
  background-color: #000;
  border: 2px solid #fff;
  border-radius: 20px;
}

.post:hover button {
  color: #fff;
  background: linear-gradient(149deg, #18bb9c 0%, #6a39af 42%, #bb1894 72%, #733586 100%) 0 0 / 400% 400%;
  animation: gradient 10s linear infinite;
}

.post:hover .returnPostMenu {
  color: #000;
  box-shadow: none;
  background: url("frame-3.7f78899e.png") no-repeat;
  border: none;
  border-radius: 0;
  transition: all 1s;
  animation: none;
  transform: scale(1);
}

.post:hover p {
  color: #000;
  background-color: #fff;
}

.post:hover .actions-container {
  opacity: 1;
  transform: translateY(0%);
}

.actions-container {
  opacity: 0;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  transition: all .3s;
  display: flex;
  transform: translateY(100%);
}

.actions-container button {
  width: 30%;
  font-size: 14px;
}

.comments {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

input {
  width: 100%;
  height: 50px;
  border: 1px solid gray;
  border-radius: 15px;
  padding: 5px;
}

textarea {
  width: 100%;
  height: 80%;
  border: 1px solid gray;
  border-radius: 15px;
  padding: 5px;
}

button {
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 10px;
}

button:hover {
  color: #fff;
  background-color: #000;
  border: none;
  transition: all .3s;
  transform: scale(1.1);
  box-shadow: 0 0 10px 1px #606069;
}

button, button:active, button:focus {
  outline: none;
  transition: all .3s;
}

.returnPostMenu {
  background: url("frame-3.7f78899e.png") no-repeat;
  border: none;
  border-radius: 0;
}

.returnPostMenu:hover {
  background-color: none;
  background: url("frame-3.7f78899e.png");
}

